'use client';

import React, {
  useEffect,
  useRef,
  useState,
  createContext,
  useContext,
  useCallback,
} from 'react';

const STICKY_TOP_REM = 5;
const STICKY_TOP_PX = STICKY_TOP_REM * 16;

/** Use full viewport height so section content is not clipped; header stays on top via z-index. */
function getViewportHeight() {
  if (typeof window === 'undefined') return 700;
  return window.innerHeight;
}

type ScrollGateContextValue = {
  progress: number;
  allowInnerScroll: boolean;
  scrollContainerRef: React.RefObject<HTMLDivElement | null>;
};
const ScrollGateContext = createContext<ScrollGateContextValue>({
  progress: 0,
  allowInnerScroll: true,
  scrollContainerRef: { current: null },
});
export function useScrollGate() {
  return useContext(ScrollGateContext);
}

/**
 * Single window scroll: sticky viewport + spacer so gate content (CommercePlatform + MercatoTools)
 * scrolls "all the way to bottom" via translateY before the Pricing layer receives scroll.
 * No inner scroll — everything is window scroll so handoff "kumakagat" reliably.
 */
export function ScrollGate({ children }: { children: React.ReactNode }) {
  const gateContainerRef = useRef<HTMLDivElement>(null);
  const contentWrapperRef = useRef<HTMLDivElement>(null);

  const [viewportHeight, setViewportHeight] = useState(getViewportHeight());
  const [contentHeight, setContentHeight] = useState(0);
  const [progress, setProgress] = useState(0);

  const spacerHeight = Math.max(0, contentHeight - viewportHeight);

  const measure = useCallback(() => {
    if (typeof window === 'undefined') return;
    setViewportHeight(getViewportHeight());
    const content = contentWrapperRef.current;
    if (content) {
      const h = content.getBoundingClientRect().height;
      setContentHeight(h);
    }
  }, []);

  useEffect(() => {
    measure();
    window.addEventListener('resize', measure);
    return () => window.removeEventListener('resize', measure);
  }, [measure]);

  useEffect(() => {
    const content = contentWrapperRef.current;
    if (!content) return;
    const ro = new ResizeObserver(() => {
      setContentHeight(content.getBoundingClientRect().height);
    });
    ro.observe(content);
    return () => ro.disconnect();
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      const gate = gateContainerRef.current;
      if (!gate || contentHeight <= 0 || spacerHeight <= 0) {
        setProgress(0);
        return;
      }
      const rect = gate.getBoundingClientRect();
      const scrollY = window.scrollY;
      const gateStartY = scrollY + rect.top;
      const p = Math.min(1, Math.max(0, (scrollY - gateStartY) / spacerHeight));
      setProgress(p);
    };

    handleScroll();
    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, [contentHeight, spacerHeight]);

  const translateY =
    contentHeight > viewportHeight
      ? -progress * (contentHeight - viewportHeight)
      : 0;

  return (
    <ScrollGateContext.Provider
      value={{
        progress,
        allowInnerScroll: false,
        scrollContainerRef: gateContainerRef,
      }}
    >
      <div ref={gateContainerRef} className="relative w-full">
        <div
          className="sticky w-full overflow-hidden rounded-t-3xl bg-[#092727]"
          style={{
            top: 0,
            height: viewportHeight,
            minHeight: '100vh',
          }}
        >
          <div
            ref={contentWrapperRef}
            className="w-full will-change-transform"
            style={{ transform: `translateY(${translateY}px)` }}
          >
            {children}
          </div>
        </div>
        <div
          aria-hidden
          className="bg-[#092727]"
          style={{ height: spacerHeight }}
        />
      </div>
    </ScrollGateContext.Provider>
  );
}
